@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap');

.latest-order {
    text-align: center;
    font-family: Roboto;
    font-size: 18px;
    letter-spacing: 0px;
    word-spacing: 0px;
    color: #000000;
    font-weight: normal;
    text-decoration: none;
    font-style: normal;
    font-variant: normal;
    text-transform: none;
}

.latest-order img {
    width: 30px;
    height: 30px;
    padding-top: 4px;
}

.latest-order header {
    width: 100%;
    height: 28px;
    display:block;
    position: sticky;
    text-align: center;
    font-size: 20px;
    background: #eee;
    padding: 2px;
    color: black;
    box-sizing: border-box;
    top: 0px;
    border: 2px solid #e8e9eb;
    z-index: 1000;
}

.latest-order table {
    width: 100%;
    border-collapse: collapse;
    box-sizing: border-box;
    table-layout: fixed;
}

.latest-order table tr {
    height: 25px;
}

.latest-order table td, th {
    padding: 0;
    white-space:nowrap;
}

.latest-order div.thead {
    display: block;
    position: sticky;
    top: 28px;
}

.latest-order thead tr {
    height: 35px;
    color: #ffffff;
    text-overflow: clip;
    font-weight: bold;
    background: #0099FF;
}

.latest-order thead th {
    border: 1px solid #6699CC;
}

.latest-order .tbody {
    overflow-x: auto;
	margin-top: 0px;
}

.latest-order tbody td {
    border: 1px solid #e8e9eb;
    overflow: hidden;
    text-overflow: ellipsis
}

.latest-order table tbody tr:nth-child(even) {
    background: #f4f4f4;
}

.latest-order .column-id {
    width: 52px;
}

.latest-order .column-status {
    width: 38px;
    overflow: hidden;
    height: 30px;
    white-space: nowrap;
}

.latest-order .column-customer {
    width: auto;
}

.latest-order .column-date {
    width: 91px;
}

.latest-order .column-total {
    width: 60px;
}