@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap');

.order-details {
    text-align: left;
    font-family: Roboto;
    font-size: 18px;
    letter-spacing: 0px;
    word-spacing: 0px;
    color: #666;
    font-weight: normal;
    text-decoration: none;
    font-style: normal;
    font-variant: normal;
    text-transform: none;
    box-sizing: border-box;
}

.return-button {
    height: 36px;
    width: 220px;
    margin: 20px;
    padding: 8px;
    display: flex;
    align-items: center;
    flex-direction: row;
    color: #000;
    text-decoration: none;
    background: #fff;
    border: 1px solid #e8e9eb;
    border-radius: 4px;
    -webkit-box-shadow: 0px 3px 5px 0px #000000; 
    box-shadow: 0px 3px 5px 0px #000000;
}

.return-button:active {
    background: #D8D9DB;
    box-shadow: 0px 0px 1px 0px #000000;
}

.return-button p {
    margin-left: 12px;
}

.return-button img {
    height: 32px;
    width: 32px;
    padding: 2px;
}

div.spacer {
    height: 16px;
}

.panel-data-container {
    padding: 6px;
}

.order-details header {
    width: 100%;
    height: 28px;
    display:block;
    position: sticky;
    top: 28px;
    text-align: center;
    font-size: 20px;
    background: #eee;
    border: 1px solid #e8e9eb;
    padding: 2px;
    color: black;
    z-index: 950;
    box-sizing: border-box;
}

.order-details .page-header {
    top: 0px;
    text-align: center;
    font-size: 20px;
    background-color: #0099FF;
    border: 2px solid #6699CC;
    z-index: 1000;
}

div.customer-info-panel {
    border: 2px solid #e8e9eb;
    border-top: 0px;
}

.customer-info-panel table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    max-width: 100%;
    text-align: left;
}

.customer-info-panel table tr {
    height: 25px;
}

.customer-info-panel .customer-info-img {
    width: 25px;
}

.customer-info-panel img {
    height: 20px;
    width: 20px;
    padding: 2px;
}

.customer-phone {
    color: #000;
    text-decoration: none;
    background: #fff;
    border: 1px solid #e8e9eb;
    border-radius: 4px;
    -webkit-box-shadow: 0px 0px 2px 0px #000000; 
    box-shadow: 0px 0px 2px 0px #000000;
    padding: 0px;
}

.customer-phone:active {
    background: #D8D9DB;
    box-shadow: 0px 0px 1px 0px #000000;
}

div.address-info-panel {
    border: 2px solid #e8e9eb;
}

.address-info p {
    margin-top: 0px;
    margin-bottom: 0px;
}

p.address-info-field {
    margin: 2px;

}

div.item-list-panel {
    border: 2px solid #e8e9eb;
}

.item-list-panel div.item-name {
    font-size: 18px;
    color: black;
    margin-bottom: 12px;
}

.item-list-panel table {
    border: 1px solid #ddd;
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    max-width: 100%;
    text-align: center;
}

.item-list-panel table tbody tr:nth-child(even) {
    background: #f4f4f4;
}

.item-list-panel table tr th {
    border: 1px solid #e8e9eb;
    text-decoration: none;
}

.item-list-panel table tr td {
    border: 1px solid #e8e9eb;
}

.item-list-panel .total {
    text-align: right;
    margin-right: 10px;
}