@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap');

*,
*:before,
*:after{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.login-panel {
  font-family: Roboto;
  font-size: 18px;
  letter-spacing: 0px;
  word-spacing: 0px;
  color: #000000;
  font-weight: normal;
  text-decoration: none;
  font-style: normal;
  font-variant: normal;
  text-transform: none;
}

.login-panel{
    height: 460px;
    width: 360px;
    position: absolute;
    transform: translate(-50%,-50%);
    top: 50%;
    left: 50%;
    border-radius: 10px;
    backdrop-filter: blur(10px);
    border: 2px solid rgba(255,255,255,0.1);
    box-shadow: 0 0 40px rgba(8,7,16,0.6);
    padding: 50px 35px;
}

.login-panel header {
    font-size: 24px;
    line-height: 42px;
    text-align: center;
}

label{
    display: block;
    margin-top: 26px;
    font-size: 16px;
}

.login-panel input{
    display: block;
    height: 50px;
    width: 100%;
    border-radius: 3px;
    padding: 0 10px;
    margin-top: 8px;
    font-size: 14px;
    font-weight: 300;
}

.login-panel button{
    margin-top: 46px;
    width: 100%;
    background-color: #0099FF;
  border: 2px solid #6699CC;
    color: #080710;
    padding: 15px 0;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
}